import '../sass/General.scss';
import Home from '../pages/Home';
import About from '../pages/About'; 

import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link
} from "react-router-dom";

function MainNav() {
  let navList = [
    {name:"Home",path:"/",node:<Home/>},
    {name:"CV",path:"/",node:<Home/>},
    {name:"Portfolio",path:"/",node:<Home/>},
    {name:"About",path:"/About",node:<About/>}];
  return (
    <Router>
      <div className="MainNav">
        <ul>
          {navList.map((obj, i)=> <li key={i}><Link to={obj.path}>{obj.name}</Link></li> )}
        </ul>
      </div>
      <Routes>
        {navList.map((obj ,i)=> <Route key={i} path={obj.path} element={obj.node}></Route>)}
      </Routes>
    </Router>
  );
}

export default MainNav;



